.login-page {
    position: relative;
  .error {
    margin-block-end: rem(20px);
    padding: rem(10px) rem(20px);
    background-color: $error;
    font-size: rem(14px);
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 4px;
    width: 100%;
  }
  header {
    height: 10vh;
    .logo {
      svg {
        -webkit-font-smoothing: antialiased;
        path {
          fill: $red;
        }
      }
    }
  }
  min-height: 100vh;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
  .background-img {
    min-height: 100vh;
    object-fit: cover;
    position: fixed;
    inset: 0;
    z-index: -1;
    filter: brightness(70%);
  }
  header {
    padding-block-start: rem(28px);
    margin: 0 rem(56px);
  }

  .form-container {
    height: 70vh;
    margin-block-end: rem(90px);
    form {
      h3 {
        align-self: flex-start;
        font-size: rem(32px);
        font-weight: 700;
        font-family: Arial, Helvetica, sans-serif;
        margin-block-end: rem(28px);
      }
      @include flex(column, flex-start);
      margin: 0 auto;
      border-radius: 4px;
      width: 450px;
      height: 100%;
      padding: rem(60px) rem(68px) rem(40px);
      margin-block-end: 90px;
      background-color: rgba(0,0,0,.75);
      label {
        width: 100%;
        height: 50px;
        position: relative;
        background-color: $input;
        color: black;
        margin-block-end: rem(40px);
        border-radius: 4px;
        padding-block-start: rem(10px);
        padding-inline-start: rem(20px);
        font-family: Arial, Helvetica, sans-serif;
        font-size: rem(11px);
        span {
          @include flex;
          position: absolute;
          cursor: pointer;
          z-index: 10;
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
          right: -41px;
          top: 50%;
          font-size: rem(14px);
          color: #8c8c8c;
          transform: translate(-50%, -50%);
          text-transform: uppercase;
          height: 100%;
          padding: 18px;
          background-color: #333;
          min-width: 80px;
        }
        input {
          background-color: transparent;
          border: none;
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
          padding-block-start: rem(20px);
          padding-inline-start: rem(20px);
          font-size: 1rem;
            &:focus {
              outline: none;
            }
        }
      }
        button {
          width: 100%;
          position: relative;
          height: 50px;
          font-weight: 700;
          font-family: Arial, Helvetica, sans-serif;
          @include button(rem(16px), rem(16px), 3px);
          img {
            position: absolute;
            transform: translate(-50%, -50%);
            width: 30px;
            height: 30px;
          }
        }
        p {
          color: $grey;
          align-self: flex-start;
          margin-block-start: rem(40px);
          a {
            color: $text;
            font-family: Arial, Helvetica, sans-serif;
          }
        }
    }
  }
  footer {
    div {
      max-width: 1000px;
      color: #757575;
      margin: 0 auto;
      padding: rem(30px) 0;
    }
    height: 180px;
    background: rgba(0,0,0,.75);
    padding-block-end: rem(20px);
  }
}
